import { Component, OnInit } from '@angular/core';
import { ApiQuotesService } from '../api-quotes.service';

@Component({
  selector: 'app-quotes',
  templateUrl: './quotes.component.html',
  styleUrls: ['./quotes.component.css']
})
export class QuotesComponent implements OnInit {

   quotes : any[]=[];
  constructor(private _api:ApiQuotesService) { }

  ngOnInit(): void {
    let ob = this._api.getAllQuotes();
    ob.subscribe((res:any)=>{
      this.quotes=res;
    });
  }

}
