import { Component, OnInit } from '@angular/core';
import { ApiHabitatService } from '../api-habitat.service';


@Component({
  selector: 'app-habitat',
  templateUrl: './habitat.component.html',
  styleUrls: ['./habitat.component.css']
})
export class HabitatComponent implements OnInit {

    habitat : any[]=[];
    constructor(private _api:ApiHabitatService) { }
  
    ngOnInit(): void {
      let ob = this._api.getAllHabitat();
      ob.subscribe((res:any)=>{
        this.habitat=res.result;
      });
    }
  
  }
